@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 21%, rgba(255,255,255,1) 100%);   
    background-repeat: no-repeat;
    background-position: cover;
    padding: 0;
    margin: 0;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    font-family: 'Jost', sans-serif;
}

.main-content {
    position: absolute;
    top: 0%;
    bottom: 0%;
    left: 22%;
    right: 0%;
    /* border: 2px pink solid; */
    padding: 2%;
    height: 78%;
}

.main-content2 {
    position: absolute;
    top: 0%;
    bottom: 0%;
    left: 22%;
    right: 0%;
    /* border: 2px pink solid; */
    padding: 2%;
    height: 78%;
}

.main-title {
    color:  #f2421b;
}

.subtitle {
    font-size: 1vw;
    color:  #f2421b;
}

.card-text {
    width: 80%;
    color: white;

}


@media only screen and (max-width: 768px) {
    
  body {
     background-image: url(data:image/jpeg;base64,);
     background-position: cover;
     z-index: 30;
  }

 

}
.sidebar {
    display: flex;
    flex-direction: column;
    align-content: center;
    background-color: #f2421b;
    width: 18%;
    /* height: 100%; */
    padding: 2% 2% 11.5% 2%;
    z-index: -2;
}

.logo {
    justify-self: center;
    color: white;
    /* color: red; */
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    border: 2px white solid;
    padding: 3%;
}

.main-links {
    color: white;
    list-style-type: none;
    font-size: 2vw;
    margin-top: 15%;
    text-shadow: 2px 2px 6px black;    
}

.link {
    margin-top: 2%;
    text-decoration: none;
    color: white;
}

.genre-links {
    color: white;
    list-style-type: none;
    font-size: 1.2vw;
    margin-top: 5%;
    height: 30%;
    overflow-y: scroll;    
}



li:hover {
    opacity: 0.5;
}
.player {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    bottom: 0;
    width: 100%;
    background-color: rgba(242, 66, 27, 0.75);
    height: 11%; 
    padding: 0.5%;
}

.player-buttons {
    display: flex;
    justify-content: space-evenly;
    /* border: 2px solid red; */
    width: 10%;  
    height: -webkit-max-content;  
    height: -moz-max-content;  
    height: max-content;
}

.player-button {
    margin: 3%;
    padding: 5%;
    color: white;
}

.player-button:hover {
    opacity: 0.5;
}

.progress-bars {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    width: 50%;
    color: white;
    /* border: red 2px solid; */
}


.hr {
    background-color: white;
    height: 3px;
    width: 55%;
}

.volume-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: blue 2px solid; */
    width: 40%;
}

.hr2 {
    background-color: white;
    height: 3px;
    width: 80%;
    margin-left: 4%;
}

.mini-bini {
    width: 8%;
    box-shadow: 4px 4px 10px black;
}

.mini-bini:hover {
    opacity: 0.5;
}


.card {
    background-color: rgba(73, 39, 42, 0.8);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1%;
    width: 40%;
    height: 45%;
}


.card2 {
    background-color: rgba(73, 39, 42, 0.8);
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1%;
    width: 40%;
    height: 80%;
}
.bini {
    width: 50%;
}
